<script>
import axios from 'axios'

export default {
  name: 'HelloWorld',
  props: {

    msg: String

  },
  data() {
    return{

      key:'3e83e523ef18a449f4eed36e9291ceae',
      art:'oficina-g3',
      mus:'a ele',
      musica:'',
    }
  },
  filters: {
    upper: function (value) {
      return value.toUpperCase();
    },
    dataFormatada: function (value) {
      return value.split("-").reverse().join("/");
    },
    duasCasasDecimais: function (value) {
      return Number(value).toFixed(2);
    },
  },
  methods: {
    getAllMeds(){
      axios
        .get(
        `https://api.vagalume.com.br/search.php?apikey=${this.key}&art=${this.art}&mus=${this.mus}`,
   
      )
      // eslint-disable-next-line
      .then((res) => {
          this.musica = (res.data.mus[0]['text'])
          console.log(res.data.mus[0]['text'])
        })
        .catch((err) => {
          console.log(err);
        });
    },


},
mounted() {

this.getAllMeds()


},
}
</script>

